.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.hero-Image {
  background-image: url(./me.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.custom-nav {
  padding: 12px 0px;
  width: 100%;
  border-radius: 0px;
  z-index: 900;
  margin-bottom: 0px;
  transition: all 0.5s ease-in-out;
  background-color: transparent;
  color: white;
}

.custom-nav .navbar-nav li a {
  color: #ffffff;
  font-size: 16px;
  background-color: transparent !important;
  padding: 15px 0;
  position: relative;
  margin: 0 7px;
  font-weight: 600;
  transition: all 0.4s;
}

.custom-nav .navbar-nav li a:before, .custom-nav.stickyadd .navbar-nav li a:before {
  content: '';
  position: absolute;
  left: 50%;
  bottom: 10%;
  height: 6px;
  width: 6px;
  background: #ffffff;
  border-radius: 50%;
  opacity: 0;
  transform: translateX(-50%);
  -webkit-transition: all 0.3s ease 0s;
  -o-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}

.navbar-toggler {
  font-size: 24px;
  margin-top: 5px;
  margin-bottom: 0px;
  color: #ffffff;
}

.Page-Section {
  min-height: 100vh;
}

.wordcloud {
  max-height: 500px;
}

.uvmlogo {
  max-height: 200px;
  max-width: 100%
}

.woodstocklogo {
  max-width: 25%;
}
.aboutParagraph {
  max-width: 50%;
  margin: 0 auto;
}

.contactParagraph {
  max-width: 25%;
  margin: 0 auto;
}

