}

.btn {
  padding: 12px 26px;
}

.btn-rounded {
  border-radius: 30px;
}

.btn-outline-custom {
  background-color: transparent;
  border: 2px solid #fff;
  color: #fff;
  letter-spacing: 1px;
  transition: all 0.5s;
  font-size: 15px;
}

.btn-outline-custom:hover,
.btn-outline-custom:focus,
.btn-outline-custom:active,
.btn-outline-custom.active,
.btn-outline-custom.focus,
.btn-outline-custom:active,
.btn-outline-custom:focus,
.btn-outline-custom:hover,
.open>.dropdown-toggle.btn-outline-custom {
  background-color: #fff !important;
  border-color: #fff !important;
  color: #000 !important;
}

.btn-outline-black {
  background-color: transparent;
  border: 2px solid #000;
  color: #000;
  font-weight: 600;
  letter-spacing: 1px;
  transition: all 0.5s;
  font-size: 15px;
}

.btn-outline-black:hover,
.btn-outline-black:focus,
.btn-outline-black:active,
.btn-outline-black.active,
.btn-outline-black.focus,
.btn-outline-black:active,
.btn-outline-black:focus,
.btn-outline-black:hover,
.open>.dropdown-toggle.btn-outline-black {
  background-color: #000 !important;
  border-color: #000 !important;
  color: #fff !important;
}

.btn-custom {
  background-color: #000;
  border: 2px solid #000;
  color: #fff;
  transition: all 0.5s;
  font-size: 15px;
  text-transform: capitalize;
}

.btn-custom:hover,
.btn-custom:focus,
.btn-custom:active,
.btn-custom.active,
.btn-custom.focus,
.btn-custom:active,
.btn-custom:focus,
.btn-custom:hover,
.open>.dropdown-toggle.btn-custom {
  background-color: #fff;
  border-color: #000;
  color: #000;
}

/*==========================
        3.NAVBAR
============================*/
.smallScreenButton{
  display: none;
}
.nameHeaderAlt{
  color: black !important;
}
.custom-nav {
  padding: 12px 0px;
  width: 100%;
  border-radius: 0px;
  z-index: 999;
  margin-bottom: 0px;
  transition: all 0.5s ease-in-out;
  background-color: transparent;
}

.custom-nav.stickyadd {
  color: black;
  background-color: #fff;
  padding: 8px 0;
  box-shadow: 0px 0px 18px 1px rgba(0, 0, 0, 0.1);
}

.custom-nav .navbar-nav li a {
  color: #ffffff;
  font-size: 16px;
  background-color: transparent !important;
  padding: 15px 0;
  position: relative;
  margin: 0 7px;
  font-weight: 600;
  transition: all 0.4s;
}

.custom-nav .navbar-nav li.active a:before,
.custom-nav .navbar-nav li:hover a:before,
.custom-nav .navbar-nav li:active a:before,
.custom-nav.stickyadd .navbar-nav li.active a:before,
.custom-nav.stickyadd .navbar-nav li:hover a:before,
.custom-nav.stickyadd .navbar-nav li:active a:before {
  opacity: 1;
}

.custom-nav .navbar-nav li a:before,
.custom-nav.stickyadd .navbar-nav li a:before {
  content: '';
  position: absolute;
  left: 50%;
  bottom: 10%;
  height: 6px;
  width: 6px;
  background: #ffffff;
  color: white;
  border-radius: 50%;
  opacity: 0;
  transform: translateX(-50%);
  -webkit-transition: all 0.3s ease 0s;
  -o-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}

.custom-nav.stickyadd .navbar-nav li a:before {
  background: #000 !important;
}

.navbar-toggler {
  font-size: 24px;
  margin-top: 5px;
  margin-bottom: 0px;
  color: #ffffff;
}

.custom-nav.stickyadd .navbar-nav li a {
  color: #000;
}

.navbar-toggle {
  font-size: 24px;
  margin-top: 5px;
  margin-bottom: 0px;
  color: #ffffff;
}

.nav .open>a,
.nav .open>a:focus,
.nav .open>a:hover {
  background-color: transparent;
  border-color: #337ab7;
}

.logo .logo-light {
  display: inline-block;
}

.stickyadd .logo .logo-dark {
  display: inline-block;
}

.stickyadd .logo .logo-light {
  display: none;
}

.logo .logo-dark {
  display: none;
}

.custom-nav .navbar-brand.logo img {
  height: 28px;
}

/*==========================
        4.HOME
============================*/

.home-bg {
  background-position: center center;
  background-size: cover;
  position: relative;
}

.home-table-center {
  display: table-cell;
  vertical-align: middle;
}

.home-table {
  display: table;
  width: 100%;
  height: 100%;
}

.header_title {
  font-size: 60px;
  font-weight: 400;
  line-height: 1.2;
  text-transform: capitalize;
}

.header_subtitle {
  line-height: 2.0;
  font-size: 18px;
  max-width: 600px;
}

.social_home li a {
  font-size: 18px;
  color: #ffffff;
  display: inline-block;
  width: 30px;
  text-align: center;
}

.scroll_down {
  position: absolute;
  bottom: 5vh;
  width: 100%;
  text-align: center;
  color: #fff;
  font-size: 13px;
  z-index: 8;
}

.scroll_down i {
  position: relative;
  -webkit-animation: bounce 2s infinite;
  animation: bounce 2s infinite;
  height: 50px;
  width: 30px;
  line-height: 60px;
  border-radius: 30px;
  border: 2px solid #ffffff;
  display: inline-block;
  color: #fff;
  font-size: 12px;
}

.scroll_down i:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  z-index: -1;
  transition: all .2s;
}

/*----DEMO 4/5/6----*/

.creative:after {
  content: "";
  position: absolute;
  bottom: -40px;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100px;
  background: transparent;
  background-size: 100% 100%;
}

/*----DEMO 7/8/9-----*/

.clip-home{
  clip-path: polygon(0 0, 100% 0, 100% 83%, 50% 100%, 0 83%);
}

/*----DEMO 10/11/12/13----*/

.jarallax{
  background-size: cover;
  background-position: center center;
}

/*----DEMO 14----*/

.home-bg-slider {
  position: relative;
}

.home-bg-slider .carousel-control-next,
.carousel-control-prev {
  width: 6%;
}

.home-bg-slider .carousel-item,
.home-bg-slider .carousel {
  height: 100vh;
  width: 100%;
}

.home-bg-slider .carousel-item {
  background-position: center center;
  background-size: cover;
}

/*==========================
        5.ABOUT ME
============================*/

.about-detail {
  max-width: 850px;
}

.about-detail .about-work li {
  font-size: 18px;
  font-weight: 600;
}

.about-detail .about-work li::after {
  content: "";
  height: 3px;
  width: 3px;
  border-radius: 50%;
  background-color: #757575;
  margin: 0px 12px;
  position: relative;
  display: inline-block;
  top: -3px;
}

.about-detail .about-work li:last-child:after {
  content: "";
  display: none;
}

.skill-custom .progress {
  height: 4px;
  margin-bottom: 40px;
  margin-top: 10px;
}

.skill-custom .progress-bar {
  background: #000;
}

/*==========================
        6.SERVICES
============================*/

.lan_box_ser {
  border: 1px dotted #e7e7e7;
}

.lan_box_ser .ser_icon i {
  font-size: 42px;
}

/*==========================
        7.FUNFACTS
============================*/

.lan_funfact {
  border: 1px dashed #b9b9b9;
}

.lan_funfact .lan_fun_name {
  font-size: 15px;
  color: rgba(255, 255, 255, 0.7);
}

.bg-funfact {
  position: relative;
  background-size: cover;
  background-position: center center;
}

/*===========================
        8.EDUCATION
=============================*/

.cv_edu_exp {
  padding: 10px 0;
  position: relative;
}

.cv_edu_exp::after {
  content: "";
  height: 100%;
  left: 0;
  background-color: #e7e7e7;
  margin: 0;
  position: absolute;
  right: 0;
  top: 0;
  width: 1px;
}

.cv_edu_exp .cv_edu_exp_box {
  border-radius: 7px;
  border: 1px solid #e7e7e7;
  margin-left: 20px;
  padding: 20px;
}

.cv_edu_exp .cv_edu_exp_box .cv_edu_exp_year {
  position: relative;
  margin: 0 0 10px 15px !important;
}

.cv_edu_exp .cv_edu_exp_box .cv_edu_exp_year::after {
  border: 3px solid #fff;
  border-radius: 50%;
  content: "";
  height: 20px;
  background-color: #000;
  position: absolute;
  left: -65px !important;
  width: 20px;
  z-index: 1;
}

.cv_edu_exp .cv_edu_exp_box .cb_edu_exp_detail {
  text-align: left;
  margin-left: 15px;
  float: left;
  border-radius: 0;
}

.cv_edu_exp h4,
.cb_edu_exp_detail h4 {
  font-size: 18px;
  font-weight: 600;
  margin-top: 0;
  text-transform: capitalize;
}

.cv_desc {
  line-height: 26px;
  font-size: 15px;
  color: #666;
}

/*==========================
        9.CTA SECTIOn
============================*/

.bg-cta {
  position: relative;
  background-size: cover;
  background-position: center center;
}

/*===========================
        10.WORK
=============================*/

.work_menu li a {
  letter-spacing: 0.03em;
  cursor: pointer;
  margin-bottom: 5px;
  font-weight: 600;
  font-size: 14px;
  color: #9a9a9a !important;
  transition: all 0.5s;
  margin-top: 5px;
}

.work_menu li {
  margin-right: 0px !important;
}

.work_menu li:after {
  content: "";
  height: 3px;
  width: 3px;
  border-radius: 50%;
  background-color: #757575;
  margin: 0px 16px;
  position: relative;
  display: inline-block;
  top: -3px;
}

.work_menu li:last-child:after {
  content: "";
  display: none;
}

.work_menu a.active,
.work_menu a:hover {
  color: #000 !important;
}

.work_item {
  position: relative;
  padding: 15px;
}

.work_box {
  overflow: hidden;
  position: relative;
}

.work_box .work_img {
  position: relative;
  overflow: hidden;
}

.work_box .work_img:after {
  content: " ";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 5px;
  transition: all 0.27s cubic-bezier(0.3, 0.1, 0.58, 1);
}

.work_box .work_img>img {
  transition: all 0.3s cubic-bezier(0.3, 0.1, 0.58, 1);
  border-radius: 0;
}

.work_box .work_detail {
  opacity: 0;
  color: #ffffff;
  width: 100%;
  padding: 20px;
  box-sizing: border-box;
  position: absolute;
  left: 0;
  overflow: hidden;
  transition: all 0.27s cubic-bezier(0.3, 0.1, 0.58, 1);
}

.work_box .work_detail h4 {
  font-size: 18px;
}

.work_box .work_detail p {
  color: rgba(255, 255, 255, 0.6);
  font-size: 14px;
}

.work_box:hover .work_detail {
  top: 50%;
  transform: translate(0, -50%);
  opacity: 1;
}

.work_box:hover .work_img:after {
  background: rgba(0, 0, 0, 0.8);
}

.work_box:hover .work_img>img {
  transform: scale(1.2);
}

/*==========================
        11.CLIENT
============================*/

.bg-client {
  position: relative;
  background-size: cover;
  background-position: center center;
}

.people_says .people_icon i {
  font-size: 60px;
}

.people_review_box .people_review {
  font-size: 18px;
  max-width: 580px;
}

.owl-theme .owl-controls .owl-page {
  margin: 20px 0px;
}

.owl-theme .owl-controls .owl-page span {
  display: block;
  width: 12px;
  height: 6px;
  margin: 5px 4px;
  filter: Alpha(Opacity=50);
  opacity: 0.5;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  border-radius: 20px;
  transition: all 0.5s;
  background: #fff;
}

.owl-theme .owl-controls .owl-page.active span {
  width: 20px;
}

/*==========================
        12.GREATE PEOPLE
============================*/

.logo_img img {
