}

/*==========================
        14.BLOG
============================*/

.blog_video .blog_play {
  position: absolute;
  top: 52%;
  left: 52%;
  margin-left: -40px;
  margin-top: -40px;
}

.blog_play {
  outline: none !important;
  width: 65px;
  height: 65px;
  padding-left: 2px;
  border-radius: 50%;
  z-index: 4;
  display: inline-block;
  cursor: pointer;
  text-align: center;
  color: #000 !important;
  font-size: 35px;
  line-height: 65px;
  background-color: #fff;
}

/*==========================
        15.CONTACT US
============================*/

.form-sub-title {
  font-size: 15px;
  line-height: 24px;
  margin-bottom: 50px;
}

.contact_form .form-control {
  box-shadow: none !important;
  height: 44px;
  border-radius: 0px;
}

.contact_form .form-control:focus {
  border-color: #000;
}

.contact_form textarea.form-control {
  height: auto;
}

.error {
  margin: 8px 0px;
  display: none;
  color: red;
}

.error_msg {
  margin-bottom: 20px;
  text-align: center;
  font-size: 18px;
  color: red;
  font-weight: 700;
}

.gig_loader {
  display: none;
}

#success_msg {
  text-align: center;
  margin-bottom: 20px;
}

#success_msg h3 {
  color: #0dba26;
  font-size: 22px;
}

/*==========================
        16.FOOTER
============================*/

.fot_social .social-icon {
  border: 2px solid #868e96;
  border-radius: 50%;
  transition: all 0.5s;
  width: 42px;
  height: 42px;
  display: block;
  font-size: 18px;
  line-height: 38px;
  text-align: center;
}

.fot_social .social-icon:hover {
  border-color: #000;
  color: #000 !important;
  transform: rotate(360deg);
